.Modal{
    width: 100vw;
    height: 100vh;
    top:0;
    position: absolute;
  }
  
  .ModalSharing{
      position: absolute;
      top: 10vh;
      left: 5vw;
      width: 90vw;
      text-align: center;
      height: 85vh;
      overflow-x: hidden;
      overflow-y: scroll;
      flex-flow: column;
      background-color: rgb(37,37,37);
      padding: 5px;
      border-radius: 10px;
      z-index: 1;
    }
  
    .selectedImage{
      box-shadow: 0 0 10px blanchedalmond ;
     }
    .Controls{
        display: flex;
        justify-content: center;
        padding: 10px;
    }
    .button{
      width: 170px;
      outline:none;
      border: none;
      margin-left: 40px;
      margin-top: 10px;
      height: 35px;
      border-radius: 5px;
      font-size: 0.9em;
      color: #101010;
      font-weight:600;
      background-color: rgb(255, 171, 0);
    }
  
    .button:hover{
      background-color: rgb(255, 206, 109);
    }
    .button:focus{
      background-color: rgb(255, 206, 109);
    }
  
  
    .Modimg{
      display: flex;
      flex-wrap: wrap;
    }
    .Modimg img{
      display: block;
      max-width: 150px;
      max-height: 150px;
      background-color: white;
      padding: 3px;
      margin: 10px;
    }